.card {
  width: 200px;
  background: #fff;
  color: #495057;
  box-sizing: border-box;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.card:hover {
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.3);
}

.card-body {
    background: #fff;
    color: #495057;
    border-radius: 4px;
}